html {
  color: #212121;
  background-color: #fff;
  font-family: verdana, arial, sans-serif;
  font-size: 22px;
  line-height: 1.3;
}

turbo-frame {
  display: block;
}

input, textarea, select, button {
  font-size: 100%;
}

a {
  color: #ec005a;
}

.turbo-progress-bar {
  background-color: #ec005a;
}

#my-frame {
  border: 5px dashed red;
  margin: 35px 0 20px;
  padding: 20px 25px;
}

#my-frame > :first-child {
  margin-top: 0;
}

#my-frame > :last-child {
  margin-bottom: 0;
}

